import { createLazyFileRoute } from "@tanstack/react-router";
import useSettingsData from "../../hooks/useSettingsData";
import { useForm } from "react-hook-form";
import useSettingsMenu from "../../hooks/useSettingsMenu";
import FormField from "../../components/Forms/FormField";
import FormFooter from '../../components/Forms/FormFooter';

const useSettingsLoader = (settingsId) => {
  const menuData = window.simplybook?.settings_menu || [];
  const settingsData = menuData.find((item) => item.id === settingsId);

  if (!settingsData) {
    throw new Error("Settings not found");
  }
  return { settingsData };
};

// Route Definition
export const Route = createLazyFileRoute("/settings/$settingsId")({
  loader: ({ params }) => useSettingsLoader(params.settingsId),
  component: Settings,
});

// Settings Component
function Settings() {
  const { settingsId } = Route.useParams();
  const { settings, saveSettings, isSavingSettings } = useSettingsData();
  const { currentForm } = useSettingsMenu();
  const currentFormFields = settings.filter(
    (setting) => setting.menu_id === settingsId,
  );
  const currentFormDefaultValues = extractFormValues(settings, settingsId);
  // Initialize useForm with default values from the fetched settings data
  const {
    register,
    handleSubmit,
      control,
    formState : { isDirty },
  } = useForm({
    defaultValues: currentFormDefaultValues,
  });

  const onSubmit = (formData) => {
    saveSettings(formData).then(() => {
      // log
      console.log('Settings saved');
    });
  };

  const lastGroup = currentForm.groups[currentForm.groups.length - 1];

  return (
    <form>
      {currentForm.groups?.map((group) => {
        const isLastGroup = lastGroup.id === group.id;
        const currentGroupFields = currentFormFields.filter(
          (field) => field.group_id === group.id,
        );

        return (
          <div key={group.id}>
            <h2>{group.title}</h2>
            {currentGroupFields.map((field) => {
              return (
                <FormField setting={field} key={field.id} control={control} />
              );
            })}
          </div>
        );
      })}

      <FormFooter isDirty={isDirty} isSavingSettings={isSavingSettings} onSubmit={handleSubmit(onSubmit)} />
    </form>
  );
}

const extractFormValues = (settings, settingsId) => {
  // Extract default values from settings data where menu_id ===  settingsId
  const defaultValues = {};
  settings.forEach((setting) => {
    if (setting.menu_id === settingsId) {
      defaultValues[setting.id] = setting.value;
    }
  });

  return defaultValues;
};
